import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Programa_qualidade } from '../../../modelo/Programa_qualidade'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'programa_qualidade-dados-details',   templateUrl: 'programa_qualidade_dadosDetails.component.html' }) export class Programa_qualidadeDadosDetailsComponent implements OnInit {    programa_qualidade: Programa_qualidade;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.programa_qualidade.Programa_qualidade";                 let id = +params['id'];         this.programa_qualidade = new Programa_qualidade();         this.programa_qualidade.cd_programa_qualidade = id;         o.objetoJson = this.programa_qualidade;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a programa_qualidade. '; }      this.programa_qualidade = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


